import * as React from "react";
import { Box } from "@mui/system";
import { Grid } from "@mui/material";
import { useSelector } from "react-redux";
import MarketplaceCard from "../../MarketplaceCard/MarketplaceCard";
import { EmptyData } from "../../AdminDashbord/EmptyData";
import { useState } from "react";
import { useEffect } from "react";
import { getUserFollowsNfts, getuserNfts } from "../../../Api/api";
import Loading from "../../../Loading/Loading";
import Paginations from "../../Paginations/Paginations";
import { useDispatch } from "react-redux";
import { AdduserNfts } from "../../../Redux/Reducer";
export default function AllProducts({
  Nfts,
  AllNfts,
  faildNFT,
  favourite,
  follow,
}) {
  let dispatch = useDispatch();
  const [totalPage, setTotalPage] = useState(0);
  const [currentPage, setCurrentPage] = useState();
  const [page, setPage] = React.useState(1);
  const [Nftsrecord, setRecord] = useState([]);
  const [loading, setLoading] = useState(false);
  const { ceilSlice } = useSelector((state) => state);
  const { userNfts, ceildetails, failsNfts, userdetails } = ceilSlice;
  const [nftmint, setNftmint] = useState(false);
  let lenOFceil = 0;
  const checker = true;
  const fetchFollowANDFavouriteNfts = async (community) => {
    setLoading(true);
    let data = await getUserFollowsNfts(community);
    setLoading(false);
    setRecord(data);
    console.log("data.....Follow:", data);
  };
  const fetchUserNfts = async (address) => {
    setLoading(true);
    let data = await getuserNfts(address, page);
    data.status === "ok" && setRecord(data?.data);
    data?.status === "ok" && dispatch(AdduserNfts(data));
    setLoading(false);
  };
  useEffect(() => {
    (async function () {
      if (Nfts === true || Nfts === false) {
        if (Nfts) {
          setRecord(ceildetails);
        }
      }
      if (AllNfts) {
        setCurrentPage(userNfts?.currentPage);
        setTotalPage(userNfts?.totalPages);
        fetchUserNfts(userdetails?.walletAddress);
      }
      if (follow) {
        if (userdetails?.follows?.length > 0) {
          fetchFollowANDFavouriteNfts(userdetails?.follows, page);
        }
      }
      if (favourite) {
        if (userdetails?.favoritesList?.length > 0) {
          fetchFollowANDFavouriteNfts(userdetails?.favoritesList, page);
        }
      }
      if (faildNFT === true) {
        setRecord(failsNfts);
      }
    })();
  }, [nftmint, page]);
  //...Pagniations here.........
  let menuItems = [];
  for (var i = 1; i <= totalPage; i++) {
    menuItems.push(i);
  }
  const paginationsHandles = (pages) => {
    favourite && fetchFollowANDFavouriteNfts(userdetails?.favoritesList);
    follow && fetchFollowANDFavouriteNfts(userdetails?.follows);
    // Nfts && fetchUserNfts(userdetails?.walletAddress);
    setPage(pages);
  };
  const PreviousButton = () => {
    setPage((page) => page - 1);
  };
  const NextButton = () => {
    setPage((page) => page + 1);
  };
  console.log("userdetails:", userdetails);
  return (
    <>
      <Loading loading={loading} />
      <Box sx={{ width: "100%", mx: "auto" }}>
        <Grid container my={2}>
          {Nftsrecord?.length > lenOFceil ? (
            Nftsrecord?.map(
              (
                {
                  address,
                  _id,
                  name,
                  lang,
                  long,
                  filelink,
                  capturesImage,
                  file,
                  productTotalPrice,
                  user,
                  favoritesList,
                  likesList,
                  howManyViews,
                  paidStatusContract,
                },
                index
              ) => (
                <Grid item xs={12} sm={6} md={4} xl={3} p={2} key={index + _id}>
                  <MarketplaceCard
                    address={address}
                    locations={{ lang, long }}
                    id={_id}
                    name={name}
                    Image={capturesImage}
                    user={user}
                    filelink={filelink}
                    file={file}
                    productTotalPrice={productTotalPrice}
                    checker={checker}
                    userlikeviews={{ likesList, favoritesList }}
                    howManyViews={howManyViews}
                    paidStatusContract={paidStatusContract}
                    failedNftmint={setNftmint}
                  />
                </Grid>
              )
            )
          ) : (
            <EmptyData text=" Empty  NFTs ...." />
          )}
        </Grid>

        {AllNfts && (
          <Paginations
            PreviousButton={PreviousButton}
            paginationsHandles={paginationsHandles}
            NextButton={NextButton}
            menuItems={menuItems}
            page={page}
            totalPages={totalPage}
          />
        )}
      </Box>
    </>
  );
}
